<template>
	<div class="template-wrapper">
		<div ref="queryBar">
			<el-form :inline="true" :model="query" class="tw-query-bar" size="small">
				<el-form-item>
					<el-date-picker v-model="query.stime" type="datetime" placeholder="开始日期"></el-date-picker>
				</el-form-item>
				<el-form-item>
					<el-date-picker v-model="query.etime" type="datetime" placeholder="结束日期"></el-date-picker>
				</el-form-item>
				<el-form-item>
					<el-select v-model="query.wgName" filterable  placeholder="工作组名称">
						<i slot="prefix" class="tw-icon iconfont icon-group"></i>
						<el-option label="全部" value=""></el-option>
						<el-option v-for="item in workGroupList" :key="item.id" :label="item.username" :value="item.username"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item>
					<el-select v-model="query.dType" placeholder="设备类型">
						<i slot="prefix" class="tw-icon iconfont icon-type"></i>
						<el-option label="全部" value=""></el-option>
						<el-option label="工作井" value="工作井"></el-option>
						<el-option label="杆塔" value="杆塔"></el-option>
						<el-option label="配电站" value="配电站"></el-option>
						<el-option label="其他设备" value="其他设备"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="handleQueryClick">查询</el-button>
				</el-form-item>
			</el-form>
		</div>
		<div class="tw-panel-table" :style="{height: height}">
			<el-table :data="tableList" border size="small" height="calc(100% - 50px)" width="100%" style="margin-bottom: 10px">
				<el-table-column type="index" label="序号" width="60"></el-table-column>
				<el-table-column prop="GZZ_name" label="工作组名称" width="200"></el-table-column>
				<el-table-column prop="SBLX" label="设备类型" width="100"></el-table-column>
				<el-table-column prop="sbmc" label="设备名称" width="200"></el-table-column>
				<el-table-column prop="WorksTime" label="检查时间" width="150">
					<template slot-scope="scope">
						<span v-text="fomarttime(scope.row.WorksTime)"></span>
					</template>
				</el-table-column>
				<el-table-column prop="phone" label="联系电话" width="100"></el-table-column>
				<el-table-column prop="company" label="公司名称" width="200"></el-table-column>
				<el-table-column prop="Remarks" label="备注"></el-table-column>
			</el-table>
			<el-pagination
					background
					layout="prev, pager, next"
					:page-size="50"
					:total="num"
					@current-change="handleQueryNext">
			</el-pagination>
		</div>
	</div>
</template>

<script>
	import _ from 'underscore'
	import axios from 'axios'
	import moment from 'moment'

	export default {
		name: "ComprehensiveStatistics",
		data() {
			return {
				query: {
					stime: '',
					etime: '',
					wgName: '',
					dType: '',
					page: 1
				},
				workGroupList: [],
				tableList: [],
				height: 0,
				num: 50
			}
		},
		mounted() {
			this.$nextTick(() => {
				this.query.stime = this.fomarttime(new Date(new Date().getTime() - 1000*60*60*24))
				this.query.etime = this.fomarttime(new Date())
				this.getWorkGroupList()
				this.getJSGD()
				window.onresize = () => {
					this.getJSGD()
				}
			});
		},
		computed: {
		},
		methods: {
			fomarttime(date){
				return moment(date).format('YYYY-MM-DD HH:mm:ss')
			},
			handleQueryNext(index){
				console.log(index)
				this.query.page = index
				this.handleQueryClick()
			},
			getWorkGroupList() {
				const baseURL = this.baseURL
				axios.get('company/getgzz', {
					baseURL
				}).then(res => {
					this.workGroupList = res.data;
				})
			},
			handleQueryClick() {
				const baseURL = this.baseURL;
				const {stime,etime,wgName,dType,page} = this.query;
				console.log(this.query)
				axios.get('company/getzhtj', {
					baseURL,
					params: {
						stime,etime,wgName,dType,page
					}
				}).then(res => {
					this.tableList = res.data.data;
					this.num = res.data.count;
					console.log(res.data.data)
				})
			},
			getJSGD(){
				let height = `calc(100% - ${this.$refs.queryBar.offsetHeight}px - 20px)`;
				this.height = height;
			}
		}
	}
</script>

<style scoped>
</style>